<template>
    <div>
<!-- 		<h1>{{blog.title}}</h1>
		<article>{{blog.content}}</article>
		<p>作者: {{blog.author}}</p>
		<p>分类:</p>
		<ul>
			<li v-for="category in blog.categories" :key='category'>
				{{category}}
			</li>
		</ul> -->
		<div class="jumbotron">
			 <div class="container">
		 <h1>{{blog.title}}</h1>
		  <p>{{blog.content}}</p>
		  <p><a class="btn btn-primary btn-lg" href="#" role="button" @click='del'>删除博客</a></p>
 		 </div>
		</div>
    </div>
</template>
<script>
    export default {
    name :'SingleBlog',   
        data(){
            return {
            	id:this.$route.params.id,
            	blog : {}
            }
        },methods:{
        	del(){
        		this.$http.delete("https://wd5813085728rxwwca.wilddogio.com/posts/"+this.id+".json").then(res=>{
        			this.$router.push({path:'/'})
        		})
        	}
        },
    	created() {
        this.$http.get("https://wd5813085728rxwwca.wilddogio.com/posts/"+this.id+".json")
  				.then(res => {
                console.log(res.body)
                this.blog = res.body
            })
    }

    }
</script>
<style scoped>

</style>



